import Vue from 'vue';
import axios from 'axios';

let util = {};
const BASE_URL = '/';
const USE_AES = false;

const instance = axios.create({
    baseURL: BASE_URL,
    method: 'post',
    timeout: 30000,
    // `transformRequest` 允许在向服务器发送前，修改请求数据
    // 只能用在 'PUT', 'POST' 和 'PATCH' 这几个请求方法
    // 后面数组中的函数必须返回一个字符串，或 ArrayBuffer，或 Stream
    transformRequest: [function (data) {
        // 对 data 进行任意转换处理
        data = JSON.stringify(data);
        return data;
    }],
    // `transformResponse` 在传递给 then/catch 前，允许修改响应数据
    transformResponse: [function (data) {
        // 对 data 进行任意转换处理
        return data;
    }],
    // `params` 是即将与请求一起发送的 URL 参数
    // 必须是一个无格式对象(plain object)或 URLSearchParams 对象
    params: {
        // ID: 12345
    },
    headers: {
        'Content-Type': 'application/json;charset=UTF-8',
        // 'channel':'03'
    },
    withCredentials: true // 允许携带cookie
});


// Add a request interceptor
instance.interceptors.request.use((config) => {
    // Do something before request is sent
    if (config.noErrorTips) {
        config.headers.common['NO_ERR_TIPS'] = 1; // 报错信息不提示
    }
    /*if (config.url === '/api/app/user/login' || config.url === '/api/app/user/register') {
      isFetchAesKeyPairRequest = true;
      config.headers.common['USE_AES'] = 0; // 不使用加密
    } else {
      if (USE_AES) {
        config.headers.common['AES-KEY'] = AES_KEY_PAIR.key;
        config.headers.common['AES-IV'] = AES_KEY_PAIR.iv;
        config.headers.common['AES-TEXT'] = JSON.stringify(config.data || {}).replace(/:/g, '=').replace(/\{|\}|"| /g, '').replace(/([^\u0000-\u00FF])/g, function ($) { return escape($); });
        config.headers.common['USE_AES'] = 1; // 使用加密
      } else {
        config.headers.common['USE_AES'] = 0; // 不使用加密
      }
      isFetchAesKeyPairRequest = false;
    }*/
    return config;
}, (error) => {
    // Do something with request error
    return Promise.reject(error);
});

// Add a response interceptor
instance.interceptors.response.use((response) => {
    // Do something with response data
    if (USE_AES && response.config.headers['USE_AES'] === 1) {
        let data = response.data || response.data.substring(1, response.data.length - 1);
        response.data = JSON.parse(crypto.aesDec(data, AES_KEY_PAIR.key, AES_KEY_PAIR.iv));
    } else {
        if (response.data) {
            response.data = JSON.parse(response.data);
            if (!response.data.success && response.config.headers['NO_ERR_TIPS'] != 1) {
                // common.msg(response.data.message);
            }
        }

    }
    return response.data;
}, (error) => {
    // Do something with response error
    return Promise.reject(error);
});

util.ajax = (url, options, headers) => {
    // if (__SERVER_ENV__ == 'sit'){
    //     instance.defaults.baseURL = 'http://52.80.43.32:9080/';
    // }
    options = options || {};
    return instance({
        url: url,
        data: options.data
    }).then(res => {
        if (res.returnCode === '000101') {
            this.$router.push('/logins')
        }
        return res;
    }).catch(err => {
        return err;
    });
};

export default util;



