<template>
    <div class="nav" @click="changeShow">
        <ul>
            <li v-for="item,index in items" :key="index">
                <span @click="routerGo(item)">{{item.title}}</span>
            </li>
        </ul>
    </div>
</template>

<script>
import Vue from 'vue'

export default Vue.component('c-nav',{
    props:['list'],
    data(){
        return {
            items:this.list
        }
    },
    methods:{
        routerGo(item){
            this.$router.push(item.path)
        },
        changeShow(){
            this.$emit('my-event')
        }

    },
    mounted() {

    },

})
</script>




