import Vue from 'vue'
import Router from 'vue-router'
import HelloWorld from '@/components/HelloWorld'
import Storage from '../libs/session/Storage'

Vue.use(Router)

const router = new Router({
    mode: 'history',
    base: '/cms',
    routes: [
        {
            path: '/',
            redirect: '/homes'
        },
        {
            path: '/HelloWorld',
            name: 'HelloWorld',
            component: HelloWorld
        },
        {
            path: '/homes',
            name: 'home',
            meta: {
                title: '',
                isNavShow: false
            },
            component: resolve => require(['../Pages/home/home.vue'], resolve)
        },
        {
            path: '/logins',
            name: 'login',
            meta: {
                title: '登录',
                isNavShow: false
            },
            component: resolve => require(['../Pages/login/index.vue'], resolve)
        },
        {
            path: '/addFeel',
            name: 'addFeel',
            meta: {
                title: '文章管理',
                isNavShow: false
            },
            component: resolve => require(['../Pages/addFeel/index.vue'], resolve)
        },
        {
            path: '/waterList',
            name: 'waterList',
            meta: {
                title: '文章列表',
                isNavShow: false
            },
            component: resolve => require(['../Pages/waterList/index.vue'], resolve)
        },
        {
            path: '/userInfo',
            name: 'userInfo',
            meta: {
                title: '用户管理',
                isNavShow: false
            },
            component: resolve => require(['../Pages/userInfo/index.vue'], resolve)
        },
        {
            path: '/keyTube',
            name: 'keyTube',
            meta: {
                title: '标题管理',
                isNavShow: false
            },
            component: resolve => require(['../Pages/keyTube/index.vue'], resolve)
        }
    ]
})

router.beforeEach((to, from, next) => {
    /* must call `next` */
    console.warn('ROUTE from --- ' + from.path)
    const loginShow = JSON.parse(Storage.sessionGet('LOGINSHOW'))

    if (loginShow || to.path === '/logins' || to.path === '/homes') {
        next()
    } else {
        next('/logins')
    }
})

router.afterEach((to, from, next) => {
    console.warn('ROUTE to --- ' + to.path)
})

export default router



