import util from './util'
import API from './APIs'

const ApiHelper = {
    login (data) {
        return util.ajax(API.login, {
            data: data
        })
    },
    logoReset (data) {
        return util.ajax(API.logoReset, {
            data: data
        })
    },
    logOut (data) {
        return util.ajax(API.logOut, {
            data: data
        })
    },
    search (data) {
        return util.ajax(API.search, {
            data: data
        })
    },
    addFeel (data) {
        return util.ajax(API.addFeel, {
            data: data
        })
    },
    serachfeel (data) {
        return util.ajax(API.serachfeel, {
            data: data
        })
    },
    searchData (data) {
        return util.ajax(API.searchData, {
            data: data
        })
    },

}

export default ApiHelper



