/*
* a component for message toast.
* author: xht
*
*/

import '../css/toast.css'

const show = (msg, callback, timeOut) => {
    typeof msg === 'string' && createDom(msg)
    setTime(callback, timeOut)
}

const createDom = (msg) => {
    const box = document.createElement('div')
    const p = document.createElement('p')
    box.setAttribute('id', 'toast')
    box.appendChild(p)
    p.innerHTML = msg
    document.body.appendChild(box)
}

const delDom = () => {
    const toastDom = document.getElementById('toast')
    document.body.removeChild(toastDom)
}

const setTime = (callback, timeOut) => {
    setTimeout(() => {
        delDom()
        callback && typeof callback === 'function' && callback()
    }, (timeOut || 2) * 1000)
}

export default show



